* {
  /* border: 1px solid; */
  box-sizing: border-box;
}

html {
  width: 1038px;
  margin: auto;
  background-color: #f5f5f0;
}

#container {
  display: flex;
  flex-direction: column;
  width: 1020px;
  row-gap: 15px;
}

footer {
  order: 3;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 10px;
  background-color: rgb(255 204 153 / 30%);
}

footer p {
  font-size: 18px;
  text-align: justify;
  line-height: 1.73;
  padding-left: 35px;
  margin-right: 30px;
  margin-top: 10px;
}

#leftblock p {
  text-align: justify;
  line-height: 1.73;
  font-size: 18px;
  margin-bottom: 54px;
  padding-left: 20px;
  padding-right: 20px;
}

#midblock p {
  font-size: 17px;
  line-height: 1.8;
  text-align: justify;
}

#midblock h1 {
  font-size: 35px;
  margin-top: 25px;
  margin-bottom: 26px;
}

#midblock h1:nth-child(1) {
  margin-top: 15px;
}

#rigthblock p {
  text-align: justify;
  line-height: 1.73;
  font-size: 18px;
  margin-top: 5px;
  padding-left: 20px;
  padding-right: 20px;
}

#midblock article {
  padding-left: 35px;
  padding-right: 60px;
  margin-bottom: 68px;
}

#head {
  order: 1;
  display: flex;
  flex-flow: row wrap;
  height: 247px;
  padding-left: 0;
  margin-left: 15px;
  margin-top: 15px;
  margin-right: 10px;
  background-color: rgb(255 204 153 / 30%);
}

nav {
  order: 2;
  width: 460px;
}

#img {
  order: 1;
  background-image: url("../images/happy.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 13px;
  height: 200px;
  width: 410px;
  margin-left: 20px;
  margin-top: 20px;
}

nav span {
  display: block;
  font-size: 20px;
  text-align: center;
  margin: 21px;
  margin-left: 21px;
  margin-left: 25px;
  border: 1px solid grey;
  border-radius: 5px;
  background-color: blanchedalmond;
}

nav span:hover {
  background-color: rgb(240 178 80);
}

nav a {
  text-decoration: none;
}

main {
  order: 2;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  height: 1085px;
  margin-left: 10px;
  margin-right: 10px;
  column-gap: 2%;
}

#leftblock {
  order: 1;
  width: 24%;
}

#midblock {
  order: 2;
  flex-grow: 2;
  width: 52%;
  padding-top: 10px;
}

#rigthblock {
  order: 3;
  width: 24%;
}

#leftblock,
#midblock,
#rigthblock {
  background-color: rgb(255 204 153 / 30%);
}
